___CSS_LOADER_EXPORT___.push([module.id, `#users-header {
    text-align: center;
}

#users {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    color: white;
}

.user-container {
  display: flex;
  padding: 10px;
  justify-content: right;
  border-radius: 5px;
  margin: 10px;
  box-shadow: rgba(0, 0, 0, 0.15) 0px 5px 15px;
}

#users a {
    text-decoration: none;
    color: black;
    margin: 10px;
}

.user {
    height: 300px;
    width: 360px;
    text-align: center;
    background-color: white;
    border-radius: 30px;
    padding: 10px;
    overflow: scroll
}

.user-pic {
    height: 100px;
    width: 100px;
    margin-top: 10px;
}

.user:not(.single-user):hover {
    height: 310px;
    width: 370px;
    transition: 0.3s
}

.user::-webkit-scrollbar {
    display: none;
}

.user-container > .delete-button {
    width: 50px;
    background-color: red;
    color: white;
    border: none;
    border-radius: 10px;
}

.delete-button:hover {
    background-color: crimson;
    transition: 0.3s
