___CSS_LOADER_EXPORT___.push([module.id, `/* User Component */
.user-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 10px;
}

.single-user {
    height: 310px;
    width: 360px;
    /* background-color: rgb(79, 137, 110); */
    border-radius: 30px;
    padding: 15px;
    text-align: right;
    box-shadow: rgba(0, 0, 0, 0.15) 0px 5px 15px;
}

.user-pic {
    height: 100px;
    width: 100px;
    margin-top: 10px;
    border: 1px solid;
    border-radius: 50%;
    background-color: azure;
}

.user::-webkit-scrollbar {
    display: none
}

button {
    border: none;
    border-radius: 10px;
    margin: 5px;
}

.back-button:hover {
    background-color: grey;
    color: white;
    transition: 0.3s
}

.edit-button {
    background-color: red;
    color: white;
    width: 50px;
}

.edit-button:hover {
    background-color: crimson;
    transition: 0.3s
}

/* EditUser Component */

.container {
    display: flex;
    justify-content: center;
    align-items: center;
}

.form-container {
    display: flex;
    flex-direction: column;
    margin: 10px;
}

.form-header {
  display: flex;
  justify-content: center;
  border-radius: 5px;
  width: 600px;
  box-shadow: rgba(0, 0, 0, 0.15) 0px 5px 15px;
}

#edit-user {
    background-color: white;
    width: 600px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 0 0 30px 30px;
    box-shadow: rgba(0, 0, 0, 0.15) 0px 5px 15px;
}

#edit-user > label {
    margin-top: 10px;
}

#edit-user > input {
    margin: 10px;
}

.edit-menu-buttons button:not(.delete-button) {
    width: 100px
}

.edit-menu-buttons button:not(.delete-button):hover {
    background-color: grey;
    color: white;
    transition: 0.3s
}

.edit-menu-buttons > .delete-button {
    width: 150px;
    background-color: red;
    color: white;
    border: none;
    border-radius: 10px;
}

.delete-button:hover {
    background-color: crimson;
    transition: 0.3s
}

.orderContainer {
    display: flex;
    flex-direction: row;
}

.orderContainer > p {
    margin-right: 8px;
    margin-left: 8px;
}

.bold {
    font-weight: 700 ;
}

.green {
    color: green
}

.red {
    color: red
}

.italic {
    font-style: italic;
}
